/* toolbars */
a {
  text-decoration: none;
}

.toolbar {
  display: flex;
  width: 100%;
  height: 64px;
  align-items: center;
  background-color: #324148;
}

.toolbar .logo {
  width: auto;
  height: 100%;
}

.toolbar .title {
  color: #ffffff;
  font-size: 24px;
}

.toolbar .title b {
  color: rgb(195, 1, 1);
}